@import "../base.scss";

.c-hero-with-products {
  &__header {
    background-color: $s-color--background-tertiary;

    &-inner {
      @include t-gutters;
      padding-top: t-to-rem(130);
      padding-bottom: t-to-rem(40);

      &.has-header-images {
        position: relative;
        height: t-to-rem(434);
      }
    }

    &-heading {
      color: $s-color--font-6;
      text-align: center;
    }

    &-image {
      height: t-to-rem(216);
      width: auto;
      position: absolute;
      bottom: 0;

      &.is-left {
        left: 0;
      }

      &.is-right {
        right: 0;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__header {
      &-inner {
        //padding-top: 13%;
        //padding-bottom: 8%;
        padding: 13rem 0 6rem;

        &.has-header-images {
          height: t-to-rem(527);
        }
      }

      &-image {
        height: t-to-rem(300);

        &.is-left {
          left: 10%;
        }

        &.is-right {
          right: 10%;
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__header {
      &-inner {
        padding: 15rem 0 8rem;
      }
    }
  }

}